import { MutableRefObject, useEffect, useRef } from "react";
import { FacebookAuthProvider, GoogleAuthProvider, isSignInWithEmailLink, signInWithEmailLink, signInWithPopup, signOut } from '@firebase/auth'
import {auth, Providers} from "./firebase"
import { emailAuth } from "./emailAuth";
import { useAuthState } from 'react-firebase-hooks/auth';

function App() {
  const emailRef = useRef() as MutableRefObject<HTMLInputElement>;
  const [user, loading, error] = useAuthState(auth)

  const handleEmailSignUp = (email: string) => {
    emailAuth(email)
    console.log("Check your e-mail and click the link to activate your account")
  }

  const handleProviderSignUp = (provider: any) => {
    signInWithPopup(auth, provider)
      .then((result) => {
        const credential = (provider === Providers.facebook ? FacebookAuthProvider : GoogleAuthProvider).credentialFromResult(result)
        const accessToken = credential?.accessToken
        const user = result.user
        console.log(credential, accessToken, user)
      }).catch((error) => {
        const errorCode = error.code
        const errorMessage = error.message
        const email = error.email
        const credential = (provider === Providers.facebook ? FacebookAuthProvider : GoogleAuthProvider).credentialFromError(error)
        // console.log(errorCode, errorMessage, email, credential)
      })
  }

  const handleLogOut = () => {
    signOut(auth).then(() => {
      console.log("signed out")
    }).catch((error) => {
      // console.log(error) rWFq1ytWLRhpHbysKUSE7oMzuHBsoXfLb4AFxh7ZMC9rUM7opVK7b2l0wZ2fPfSZU34
    })
  }

  // On email Sign Up - After clicking on the link in the email, check the email from local storage with the link
  // Else if the user open the email from another browser/device, prompt a window to add the email
  // to check if its the correct email
  useEffect(() => {
    if(isSignInWithEmailLink(auth, window.location.href)) {
        let LS = JSON.parse(localStorage.getItem('emailToSignUp') || '{}')
        let email = LS.email
        if(!email) {
          email = window.prompt("Te rog adauga email-ul pentru confirmare.")
        }
        console.log(email)
        signInWithEmailLink(auth, email, window.location.href)
          .then((result) => {
              window.localStorage.removeItem('emailToSignUp')
              // newUser = false in database
              // console.log(result.user, result)
          })
          .catch((error) => {
              // console.log(error.code, error.message)
          })
    }
  }, [])


  // Check if user has email with facebook sign up, if not, then prompt window to add an email
  // and use email auth after that 
  useEffect(() => {
    if (user && !user.email) {
      handleLogOut()
      let email = window.prompt("Adauga adresa ta de e-mail pentru a putea continua")
      if (email) {
        handleEmailSignUp(email)
        // console.log(email)
      } else {
        //history.push("/")
      }
    }
  }, [user])

  return (
    <div className="App">
      <input type="email" placeholder="email" ref={emailRef}  />
      <button onClick={() => handleEmailSignUp(emailRef.current.value)}>Sign in with Email</button>
      <button onClick={() => handleProviderSignUp(Providers.google)}>Sign in with Google</button>
      <button onClick={() => handleProviderSignUp(Providers.facebook)}>Sign in with Facebook</button>
      <button onClick={handleLogOut}>Log out</button>
      <div>{user?.email + ", " + user?.displayName}</div>
    </div>
  );
}

export default App;
